import React, {useState, useEffect} from 'react';
import {ImageBackground, Image, PixelRatio} from 'react-native';
let defImage =
  'data:image/jpeg;base64,';
const ScaledImage: React.FC<{uri?: string}> = ({uri = defImage}) => {
  const [state, setBaseState] = useState({source: {uri}, width: 0, height: 0});
  const setState = (newState: any) => setBaseState({...state, ...newState});
  useEffect(() => {
    Image.getSize(
      uri,
      (width: number, height: number) => {
        setState({
          width: PixelRatio.roundToNearestPixel(width),
          height: PixelRatio.roundToNearestPixel(height),
        });
      },
      () => {},
    );
  }, []);
  return (
    <ImageBackground
      source={state.source}
      style={{
        flex: 1,
        aspectRatio: 1,
        height: '100%',
        width: '100%',
        //   resizeMode: 'cover',
      }}
    />
  );
};
export default ScaledImage;
